.App {
  width: 100%;
  height: 100%;
  display: flex;
  box-sizing: border-box;
}

.networkContainer {
  flex: 0 0 70%;
}

.inputContainer {
  flex: 0 0 30%;
  border-left: 1px solid black;
  height: 100%;

  overflow-y: scroll;
}

.formContainer {
  padding: 10px;
  border-bottom: 1px solid black;
}

.formContainer h6 {
  margin: 0px;
  text-transform: uppercase;
}

.red {
  color: red;
}
